.footer_block {
  text-decoration: none; /* Removes the underline */
  color: inherit;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  .footer-copyright-name {
    display: block;
    @include adaptiv-value(width, 30, 15, 0);
    @include adaptiv-value(height, 15, 7, 0);
    margin-left: 8px;
    // width: 100px;
    // height: 100px;
    // margin: 10px;
    // background-color: red;
  }

  .footer-copyright-name:hover {
    scale: 1.1;
  }
