@import "./mixins.scss";

.subtitle {
  //   width: 500px;
  @include adaptiv-value(min-width, 500, 100, 0);
  @include adaptiv-font(40, 20);
  margin: 0 auto;
  background-color: black;
  text-align: center;
  font-weight: 600;
  color: white;
  border-radius: 25px;
  padding: 8px;
}

.header-descr {
  position: absolute;
  top: 0;
  right: 0;
  margin-top: 20px;
  margin-right: 20px;
  display: flex;
  flex-direction: column;
}

.header-descr span {
  font-family: "Josefin Sans", sans-serif;
  display: block;
  text-align: right;
  margin-top: 10px;
}
