.description_block {
  display: flex;
  flex-direction: row;
  width: 100%;
  margin-top: 50px;
  padding: 30px;
}

.descr_img {
  width: 50%;
  height: 100vh;
  //   background-color: red;
  img {
    border-radius: 50px;
  }
}

.descr_text {
  width: 50%;
  text-align: center;
  //   background-color: blue;
  font-size: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 25px;
  div {
    margin-top: 20px;
  }
}
